import * as util from "./util";

// 配置
//方向容差
const variance = 10;
//距离计算比例
const distenceRatio = 1;

//计算两点距离
let getDistence = function(p1, p2) {
    return parseInt(Math.sqrt((p1.x - p2.x) * (p1.x - p2.x) + (p1.y - p2.y) * (p1.y - p2.y)) * distenceRatio * 10) / 10
};
// 计算一段距离
let getPartDistence = function(array) {
    let result = 0;
    let start = 0;
    for (; start < array.length - 1; start++) {
        let part = getDistence(array[start], array[start + 1]);
        result += part;
    }
    return parseInt(result * 10) / 10;
}

// 画线
let paintLine = function(array, config) {
    let conf = Object.assign({
        color: '#333',
        width: 2
    }, config || {});
    let points = [];
    array.forEach(function(e) {
        points.push(e.x + ',' + e.y)
    });
    return '<polyline points="' + points.join(' ') + '" style="fill:none;stroke:' + conf.color + ';stroke-width:' + conf.width + '"/>';
}

//导航信息
let navigate = function(array, callback) {
    let result = [];
    let start = 0;
    for (; start < array.length - 1; start++) {
        let start_p = array[start],
            end_p = array[start + 1],
            distence = getDistence(start_p, end_p) + '米 ',
            tipText = '',
            shopInfo = end_p.info ? ('到达 '+end_p.info.attrTitle) : '',
            direction = '';
        if(start>0){
            let prev_p = array[start-1];
            let currentK = (end_p.y - start_p.y) / (end_p.x - start_p.x);
            let prevK = (end_p.y - prev_p.y) / (end_p.x - prev_p.x);
            let tan = (currentK- prevK)/(1+ prevK*currentK);
            let atan = Math.atan(tan)*180/Math.PI;
            if(atan>0){
                direction = "右转"+Math.abs(parseInt(atan))+"度 "
            }else{
                direction = "左转"+Math.abs(parseInt(atan))+"度 "
            }
        }
        
        if (end_p.x - start_p.x > variance) {
            //向东
            if (end_p.y - start_p.y > variance) {
                //向南
                tipText = '向东南方向直行';
            } else if (start_p.y - end_p.y > variance) {
                //向北
                tipText = '向东北方向直行';
            } else {
                //正东
                tipText = '向东直行';
            }
        } else if (start_p.x - end_p.x > variance) {
            //向西
            if (end_p.y - start_p.y > variance) {
                //向南
                tipText = '向西南方向直行';
            } else if (start_p.y - end_p.y > variance) {
                //向北
                tipText = '向西北方向直行';
            } else {
                //正西
                tipText = '向西直行';
            }
        } else {
            //向正南正北
            if (end_p.y - start_p.y > variance) {
                //向南
                tipText = '向南直行';
            } else if (start_p.y - end_p.y > variance) {
                //向北
                tipText = '向北直行';
            } else {
                console.warn('误差内的无效移动')                
            }
        }
        result.push(direction + tipText + distence + shopInfo);
    }
    result.push('到达目的地');
    typeof callback === 'function' && callback(result);
}

// 判断重合
let isEqual = function(a, b) {
    return a && b && a.x === b.x && (a.y === b.y)
}

// 分拆路径
let splitArray = function(array, index) {
    let results = [];
    if (index === 0) {
        results.push(array)
    } else if (index === array.length - 1) {
        results.push(array.reverse());
    } else {
        results.push(array.slice(0, index + 1).reverse());
        results.push(array.slice(index));
    }
    return results;
}
let pointInArray = function(point, array) {
    let isIn = false;
    for (let tryIndex = 0; tryIndex < array.length; tryIndex++) {
        if (isEqual(array[tryIndex], point)) {
            isIn = true;
            break;
        }
    }
    return isIn;
}
// 寻路
let availableRoads = []; //备选路径
let roadData = [];
let searchRoads = function(partRoads, targetPoint, excludeRoadIndex, baseRoad, sourcePoint) {
    // 待查路径 目标点 当前路径序号（扩展时将排除） 继承路径 来源断点
    if(initRoadIndex===null){
        initRoadIndex = excludeRoadIndex
    }
    if (!Array.isArray(baseRoad)) {
        baseRoad = [];
    }
    // 遍历路径
    for (let roadIndex = 0; roadIndex < partRoads.length; roadIndex++) {
        let tryRoad = [].concat(baseRoad);
        let road = partRoads[roadIndex];
        let extendPoints = [];

        // 遍历点
        for (let pointIndex = 0; pointIndex < road.length; pointIndex++) {
            let point = road[pointIndex];
            //过滤与自己相交的路线
            let isRoadRepeat = pointInArray(point, tryRoad);
            if (isRoadRepeat) {
                //console.warn('自相交' + JSON.stringify(point) + 'road ' + JSON.stringify(road))
                tryRoad.slice(0, -pointIndex);
                break;
            }
            //前进一步
            tryRoad.push(point);
            //console.warn(JSON.stringify(tryRoad))
            //收集扩展点
            if (point.connect) {
                // 排除终点 排除来源节点
                //console.warn(JSON.stringify(sourcePoint))
                if (!isEqual(targetPoint, point) && !isEqual(point, sourcePoint)) {
                    extendPoints.push(pointIndex);
                }
            }
            // 寻路成功
            if (isEqual(point, targetPoint)) {
                let newPath = [].concat(tryRoad)
                if(isReverse){
                    newPath = newPath.reverse();
                }
                availableRoads.push(newPath);
                break;
            }
        }

        // 扩展寻路
        if (extendPoints.length) {

            extendPoints.forEach(function(extIndex) {
                let extPoint = road[extIndex];
                //从扩展点处剪裁原路径
                let tryRoadNew = baseRoad.concat(road.slice(0, extIndex));
                let extendRoads = filterRoadByPoint(extPoint, excludeRoadIndex);

                extendRoads.forEach(function(extendRoad) {
                    //console.warn('扩展'+JSON.stringify(extendRoad.roads))
                    searchRoads(extendRoad.roads, targetPoint, extendRoad.roadDataIndex, tryRoadNew, extPoint);
                });
            });
        }
    }
}
// 根据链接点过滤路径
function filterRoadByPoint(connectPoint, excludeRoadIndex) {
    let theRoads = [];
    let excludeRoad = [].concat(excludeRoadIndex)
    if(Array.isArray(initRoadIndex)){
        excludeRoad = excludeRoad.concat(initRoadIndex)
    }
    roadData.forEach(function(road, i) {
        if (!excludeRoad.includes(i)) {
            for (let pointIndex = 0; pointIndex < road.length; pointIndex++) {
                let point = road[pointIndex];
                if (isEqual(point, connectPoint)) {
                    theRoads.push({
                        roads: splitArray(road, pointIndex),
                        roadDataIndex: [i]
                    });
                    break
                }
            }
        }
    });
    return theRoads;
}

let catchAvailableRoads = function(callback) {
    if (!availableRoads.length) {
        callback()
        return console.warn('寻路失败');
    }
    console.log('可选路径', availableRoads)
    // 查找最优解
    let results_distance = [];
    availableRoads.forEach(function(road) {
        results_distance.push(getPartDistence(road))
    });
    let min_distence = Math.min.apply(Math, results_distance);
    let min_distence_index;
    for (let i = 0; i < results_distance.length; i++) {
        if (results_distance[i] === min_distence) {
            min_distence_index = i;
            break;
        }
    }

    if (typeof callback === 'function') {
        callback(util.deepcopy(availableRoads[min_distence_index]));
    }
    availableRoads.length = 0;
}
// 启动寻路
let initRoadIndex = null; // 初始路径序号
let isReverse = false;
let startSearch = function(array, nav_start, nav_end, callback) {
    console.log(JSON.stringify(nav_start), JSON.stringify(nav_end))
    roadData = array;
    for (let roadIndex = 0; roadIndex < roadData.length; roadIndex++) {
        let road = roadData[roadIndex];
        let getStart = false;
        for (let pointIndex = 0; pointIndex < road.length; pointIndex++) {
            let point = road[pointIndex];
            if (isEqual(point, nav_start)) {
                searchRoads(splitArray(road, pointIndex), nav_end, [roadIndex]);
                getStart = true;
                break;
            }
            if (isEqual(point, nav_end)) {
                isReverse = true;
                searchRoads(splitArray(road, pointIndex), nav_start, [roadIndex]);
                getStart = true;
                break;
            }
        }
        if (getStart) {
            break;
        }
    }
    // 寻路结果处理
    catchAvailableRoads(callback)

}

export {
    getDistence,
    getPartDistence,
    paintLine,
    navigate,
    isEqual,
    splitArray,
    startSearch
}